.section {
  display: grid;
  grid-template-rows: 1fr min-content;
  padding: 15px;
  padding-bottom: 70px;
  min-height: 100vh;
  color: #c9b37e;
  line-height: 1.2;
  background-image: linear-gradient(-180deg, #170202 0, #0c0101 100%);
}

.wrapper {
  width: 420px;
  margin: auto;
  padding-top: 45px;
  padding-bottom: 45px;
  text-align: center;
  margin-bottom: auto;
}
