.heading {
  line-height: 1.2;
  font-size: 24px;
}

.link {
  border: 1px solid rgba(223, 207, 119, .36);
  border-radius: 8px;
  height: 54px;
  color: #d9cd8d;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  text-decoration: none;
  transition: color .2s, border-color .2s;
}

.link[href]:focus,
.link[href]:hover {
  border-color: rgba(84, 80, 62, .36);
  color: #54503e;
}
